            <h1 class="title" > </h1>
        </div>
        <div class="item-wrap">
            <div class="item-box" style="margin-top: 25px;">
                <div class="row">
                    <List 
                        :contents="[
                            '(Chemicals)          ',
                            '         '
                        ]"
                    />
                </div>
            </div>
            <div class="img-row">
                <img class="img16" src="@/assets/img/chemilaw_img16.png" />
            </div>
        </div>
    </div>
</template>
<script>
import List from './StyledList';
export default {
    name: 'MSDS1',
    components: {
        List
    }
}
</script>



