<template>
    <div class="contents_wrap">
        <div class="header">
            <div class="cover">
                <div class="location_wrap">
                    <h1 class="title">
                        <span v-if="sub_title==''"></span>
                        <span v-else-if="sub_title==''">  </span>
                        <span v-else-if="sub_title==''">  </span>
                        <span v-else-if="sub_title==''">    </span>
                        <span v-else-if="sub_title==''">   </span>
                        <span v-else-if="sub_title==''"> </span>
                        <span v-else-if="sub_title==''"></span>
                    </h1>
                    <div class="bar">
                        <div class="point_bar"></div>
                    </div>
                    <span class="location"> 
                        <span v-if="sub_title==''"></span>
                        <span v-else-if="sub_title==''">  </span>
                        <span v-else-if="sub_title==''">  </span>
                        <span v-else-if="sub_title==''">    </span>
                        <span v-else-if="sub_title==''">   </span>
                        <span v-else-if="sub_title==''"> </span>
                        <span v-else-if="sub_title==''"></span>
                    </span>
                </div>
            </div>
        </div>
        <div class="contents">
            <div class="sidebar">
                <div class="sidebar-box">
                    <router-link to="/chemistrylaw"><h1 class="title"></h1></router-link>
                    <div class="bar">
                        <div class="bold-bar"></div>
                    </div>
                    <div class="navbtns">
                        <router-link to="/chemistrylaw/chemistrylaw" class="btn" :class="{active : sub_title==''}"></router-link>
                        <router-link to="/chemistrylaw/beforedeclaration" class="btn" :class="{active : sub_title==''}">  </router-link>
                        <router-link to="/chemistrylaw/beforesignup" class="btn" :class="{active : sub_title==''}">  </router-link>
                        <router-link to="/chemistrylaw/newchemi" class="btn" :class="{active : sub_title==''}">    </router-link>
                        <router-link to="/chemistrylaw/exemption" class="btn" :class="{active : sub_title==''}">   </router-link>
                        <router-link to="/chemistrylaw/polymercompound" class="btn" :class="{active : sub_title==''}"></router-link>
                        <router-link to="/chemistrylaw/intermediate" class="btn" :class="{active : sub_title==''}"></router-link>
                    </div>
                </div>
            </div>
            <transition name="fade" mode="out-in">
                <router-view />
            </transition>
        </div>
    </div>
</template>
<script>
export default {
    name: 'CommunityPage',
    props: ['main_title', 'sub_title']
}
</script>



