<template>
    <div class="contents_wrap">
        <div class="header">
            <div class="cover">
                <div class="location_wrap">
                    <h1 class="title">
                        <span v-if="sub_title==''"> </span>
                        <span v-else-if="sub_title==''"> </span>
                        <span v-else-if="sub_title==''"> </span>
                    </h1>
                    <div class="bar">
                        <div class="point_bar"></div>
                    </div>
                    <span class="location">  
                        <span v-if="sub_title==''"> </span>
                        <span v-else-if="sub_title==''"> </span>
                        <span v-else-if="sub_title==''"> </span>
                    </span>
                </div>
            </div>
        </div>
        <div class="contents">
            <div class="sidebar">
                <div class="sidebar-box">
                    <router-link to="/registration"><h1 class="title"> </h1></router-link>
                    <div class="bar">
                        <div class="bold-bar"></div>
                    </div>
                    <div class="navbtns">
                        <router-link to="/registration/registration1" class="btn" :class="{active : sub_title==''}"> </router-link>
                        <router-link to="/registration/registration2" class="btn" :class="{active : sub_title==''}"> </router-link>
                        <router-link to="/registration/registration3" class="btn" :class="{active : sub_title==''}"> </router-link>
                    </div>
                </div>
            </div>
            <transition name="fade" mode="out-in">
                <router-view />
            </transition>
        </div>
    </div>
</template>
<script>
export default {
    name: 'IntroducePage',
    props: ['title', 'sub_title']
}
</script>



