<template>
  <div ref="app" id="app" v-on:scroll.passive="onScroll">
    <div class="navbar" v-if="page_title != 'admin'" :class="{background : navbackground || mobile_menu_opened, opened : mobile_menu_opened}">
      <router-link to="/"><img class="logo" src="@/assets/img/logo_img_h_w.png" /></router-link>
      <div class="menu" :class="{opened: mobile_menu_opened}">
        <div class="btn-wrap" :class="{opened: dropdown_num==0}" @mouseover="dropdown_num=0" @mouseleave="dropdown_num=-1">
          <span class="name" :class="{active : page_title==''}"> </span>
          <img src="@/assets/img/icon_down_arrow.png" />
          <transition name="dropdown">
            <div v-if="dropdown_num==0" class="dropdown">
              <span @click="mobile_menu_opened=false"><router-link to="/introduce/intro" class="btn" :class="{active : page_sub==''}"> </router-link></span>
              <span @click="mobile_menu_opened=false"><router-link to="/introduce/location" class="btn" :class="{active : page_sub==''}"> </router-link></span>
            </div>
          </transition>
        </div>
        <div class="btn-wrap" :class="{opened: dropdown_num==1}" @mouseover="dropdown_num=1" @mouseleave="dropdown_num=-1">
          <span class="name" :class="{active : page_title==''}"></span>
          <img src="@/assets/img/icon_down_arrow.png" />
          <transition name="dropdown">
            <div class="dropdown" v-if="dropdown_num==1">
              <span @click="mobile_menu_opened=false"><router-link to="/chemistrylaw/chemistrylaw" class="btn" :class="{active : page_sub==''}"></router-link></span>
              <span @click="mobile_menu_opened=false"><router-link to="/chemistrylaw/beforedeclaration" class="btn" :class="{active : page_sub==''}">  </router-link></span>
              <span @click="mobile_menu_opened=false"><router-link to="/chemistrylaw/beforesignup" class="btn" :class="{active : page_sub==''}">  </router-link></span>
              <span @click="mobile_menu_opened=false"><router-link to="/chemistrylaw/newchemi" class="btn" :class="{active : page_sub==''}">    </router-link></span>
              <span @click="mobile_menu_opened=false"><router-link to="/chemistrylaw/exemption" class="btn" :class="{active : page_sub==''}">   </router-link></span>
              <span @click="mobile_menu_opened=false"><router-link to="/chemistrylaw/polymercompound" class="btn" :class="{active : page_sub==''}"></router-link></span>
              <span @click="mobile_menu_opened=false"><router-link to="/chemistrylaw/intermediate" class="btn" :class="{active : page_sub==''}"></router-link></span>
            </div>
          </transition>
        </div>
        <div class="btn-wrap" :class="{opened: dropdown_num==2}" @mouseover="dropdown_num=2" @mouseleave="dropdown_num=-1">
          <span class="name" :class="{active : page_title==''}"></span>
          <img src="@/assets/img/icon_down_arrow.png" />
          <transition name="dropdown">
            <div v-if="dropdown_num==2" class="dropdown">
              <span @click="mobile_menu_opened=false"><router-link to="/confirmsystem/confirmsystem1" class="btn" :class="{active : page_sub==''}"> </router-link></span>
              <span @click="mobile_menu_opened=false"><router-link to="/confirmsystem/confirmsystem2" class="btn" :class="{active : page_sub==''}"></router-link></span>
            </div>
          </transition>
        </div>
        <div class="btn-wrap" :class="{opened: dropdown_num==3}" @mouseover="dropdown_num=3" @mouseleave="dropdown_num=-1">
          <span class="name" :class="{active : page_title=='MSDS'}">MSDS</span>
          <img src="@/assets/img/icon_down_arrow.png" />
          <transition name="dropdown">
            <div v-if="dropdown_num==3" class="dropdown">
              <span @click="mobile_menu_opened=false"><router-link to="/msds/msds1" class="btn" :class="{active : page_sub=='MSDS'}">MSDS   </router-link></span>
              <span @click="mobile_menu_opened=false"><router-link to="/msds/msds2" class="btn" :class="{active : page_sub=='MSDS'}">MSDS   </router-link></span>
            </div>
          </transition>
        </div>
        <div class="btn-wrap" :class="{opened: dropdown_num==4}" @mouseover="dropdown_num=4" @mouseleave="dropdown_num=-1">
          <span class="name" :class="{active : page_title==''}"> </span>
          <img src="@/assets/img/icon_down_arrow.png" />
          <transition name="dropdown">
            <div v-if="dropdown_num==4" class="dropdown">
              <span @click="mobile_menu_opened=false"><router-link to="/registration/registration1" class="btn" :class="{active : page_sub==''}"> </router-link></span>
              <span @click="mobile_menu_opened=false"><router-link to="/registration/registration2" class="btn" :class="{active : page_sub==''}"> </router-link></span>
              <span @click="mobile_menu_opened=false"><router-link to="/registration/registration3" class="btn" :class="{active : page_sub==''}"> </router-link></span>
            </div>
          </transition>
        </div>
        <div class="btn-wrap" :class="{opened: dropdown_num==5}" @mouseover="dropdown_num=5" @mouseleave="dropdown_num=-1">
          <span class="name" :class="{active : page_title==''||page_title==''}"></span>
          <img src="@/assets/img/icon_down_arrow.png" />
          <transition name="dropdown">
            <div v-if="dropdown_num==5" class="dropdown">
              <span @click="mobile_menu_opened=false"><router-link to="/community/community1" class="btn" :class="{active : page_sub==''}"></router-link></span>
              <span @click="mobile_menu_opened=false"><router-link to="/community/community2" class="btn" :class="{active : page_sub==''}"></router-link></span>
            </div>
          </transition>
        </div>
      </div>
      <div class="menu-btn" @click="mobile_menu_opened=!mobile_menu_opened">
        <div class="bar1"></div>
        <div class="bar2"></div>
        <div class="bar3"></div>
      </div>
    </div>
    <transition name="fade" mode="out-in">
      <router-view :sub_title="page_sub"/>
    </transition>
    <div class="footer" v-if="page_title != 'admin'">
      <router-link to="/"><img class="logo" src="@/assets/img/logo_img_v_w.png" /></router-link>
      <div class="text-area">
        <div class="row">
          <span class="txt">  30 31 2 1405</span>
          <span class="txt">TEL: 02-6964-7119</span>
          <span class="txt">FAX: 02-867-7079</span>
        </div>
        <div class="row">
          <span class="txt"> | ()</span>
          <span class="txt"> | </span>
          <span class="txt"> | 121-86-41700</span>
        </div>
        <div class="row-bar"></div>
        <div class="row">
          <span class="txt">Copyright  2020 () All Right Reserved.</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'App',
  data() {
    return {
      navbackground: false,
      page_title: '',
      page_sub: '',
      mobile_menu_opened: false,
      dropdown_num: -1,
    }
  },
  created() {
    this.page_title = this.$route.matched[this.$route.matched.length-1].meta.title;
    this.page_sub = this.$route.matched[this.$route.matched.length-1].meta.sub;
  },
  mounted () {
    window.addEventListener('scroll', this.onScroll)
  },
  beforeDestroy () {
    window.removeEventListener('scroll', this.onScroll)
  },
  methods: {
    'onScroll' : function() {
      this.navbackground=document.documentElement.scrollTop>0;
    }
  },
  watch: {
    '$route' (to, from) {
      this.page_title = to.meta.title;
      this.page_sub = to.meta.sub;
    }
  }
}
</script>

<style src="@/assets/css/style.css">
</style>



